import { Component, h, Host } from '@stencil/core';
import { cart } from '../../../global/data/cart';

@Component({
  tag: 'ks-cart-deal-container',
  styleUrl: 'dealContainer.css'
})
export class CartDealContainer {

    render() {
        const count = cart.get("dealGroups")?.length + cart.get("deals")?.length;

        return <Host class={!count ? "empty" : ""}>
            {cart.get("dealGroups").map((group) => 
                <ks-cart-deal
                    key={group.name}
                    name={group.name}
                    deals={group.deals}
                ></ks-cart-deal>
            )}
            {cart.get("deals").map((deal) => 
                <ks-cart-deal
                    key={deal.id}
                    name={deal.name}
                    deals={[deal]}
                ></ks-cart-deal>
            )}
        </Host>;
    }
}